@import 'styles/var.scss';

.order_detail {
	&__header {
		position: relative;
		margin-bottom: 20px;

		.number {
			font-size: 27px;
			line-height: 1;
			color: $black;
			font-weight: bold;
			display: inline-block;
			margin-right: 10px;
		}

		.dot {
			&::after {
				padding-left: 10px;
				content: '•';
			}
		}

		.status {
			display: inline-block;
			margin-right: 10px;
		}

		.deadline {
			font-size: 18px;
			line-height: 1;
			letter-spacing: 0.3px;
			color: $black;
		}

		.budget {
			font-size: 18px;
			line-height: 1;
			color: $black;
			padding: 0;

			span {
				font-size: 25px;
				color: #337fd7;
				font-weight: bold;
			}
		}
	}

	&__box {
		background: $white;
		border: 1px solid #e2e5ed;
		box-sizing: border-box;
		box-shadow: inset 0px 1px 2px rgba(102, 113, 123, 0.21);
		border-radius: 3px;
		padding: 15px;
	}

	&__wrapper {
		height: 100%;
		padding-bottom: 20px;
	}

	&__original {
		margin-bottom: 20px;
		height: 100%;

		.name {
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.3px;
			color: #337fd7;
		}

		.options {
			font-size: 15px;
			line-height: 22px;
			letter-spacing: 0.28125px;
			color: #767676;
			margin-bottom: 10px;
		}

		.description {
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.3px;
			color: #141414;
			margin-bottom: 20px;
		}

		.photos {
			&__title {
				font-size: 16px;
				line-height: 22px;
				letter-spacing: 0.3px;
				color: #141414;
				display: block;
				margin-bottom: 10px;
			}

			.images_gallerry__item {
				width: 100px;
				height: 100px;
				margin-right: 10px;
				margin-bottom: 10px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 6px;
				}
			}
		}
	}

	&__sumary {
		margin-bottom: 20px;
		height: 100%;

		.body {
			margin-bottom: 15px;
		}

		.desciption {
			font-size: 16px;
			line-height: 22px;
			letter-spacing: 0.3px;
			color: #141414;
			margin-bottom: 20px;
		}

		.photos {
			&__title {
				font-size: 16px;
				line-height: 22px;
				letter-spacing: 0.3px;
				color: #141414;
				display: block;
				margin-bottom: 10px;
			}

			.images_gallerry__item {
				width: 100px;
				height: 100px;
				margin-right: 10px;
				margin-bottom: 10px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 6px;
				}
			}

			&__upload {
				margin-top: 15px;
			}
		}
	}

	&__customer {
		margin-bottom: 20px;
	}

	&__ctas {
		margin: 20px 0;

		.cta {
			min-width: 200px;
			border-radius: 4px;
		}

		.cta2 {
			min-width: 160px;
		}
	}

	&__worklog {
		margin-bottom: 20px;
	}

	&__assigned {
		.assign__artist .order__toggle {
			width: 100%;
			font-size: 18px;
			line-height: 1;
			color: $black;
			padding: 0;

			.name {
				display: inline-block;
				white-space: nowrap;
				max-width: 150px;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
	}

	&__work_group {
		margin-bottom: 30px;

		.group {
			&__header {
				display: flex;
				align-items: center;
				justify-content: space-between;
				position: relative;
				margin-bottom: 20px;
				cursor: pointer;

				&::after {
					content: '';
					position: absolute;
					z-index: 2;
					width: 100%;
					height: 1px;
					top: 50%;
					left: 0;
					background: #eaeaea;
				}

				.dot {
					&::after {
						padding-left: 10px;
						content: '•';
					}
				}

				.deadline {
					margin-left: 10px;
				}
			}

			&__title {
				white-space: nowrap;
				position: relative;
				z-index: 3;
				display: flex;
				align-items: center;
				background: #ebebeb;
				border-radius: 0px 15px 15px 0px;
				font-size: 14px;
				line-height: 17px;
				text-transform: uppercase;
				color: #767676;
				padding: 8px 30px;
				left: -15px;

				.state {
					font-weight: 600;
				}
			}

			&__toggle {
				background: $white;
				border: 1px solid #d8d8d8;
				width: 35px;
				height: 35px;
				border-radius: 100%;
				position: relative;
				z-index: 3;
			}

			&__body {
				padding-left: 40px;
				margin-left: 70px;
				border-left: 1px solid #eaeaea;

				&.isNewOrder {
					padding-left: 0;
					margin-left: 0;
					border-left: 1px solid transparent;
				}
			}
		}
	}

	&__artwork {
		position: relative;

		.upload {
			.upload-file__form {
				min-height: 110px;
			}
		}

		.box__header {
			padding-top: 10px;
			padding-bottom: 10px;
			cursor: pointer;
		}

		.box__title {
			font-weight: 600;
			font-size: 18px;
			line-height: 21px;
			color: #141414;
		}

		.photos {
			margin-bottom: 10px;

			.images_gallerry__item {
				width: 100px;
				height: 100px;
				margin-right: 10px;
				margin-bottom: 10px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		.box__icon {
			cursor: pointer;
			position: absolute;
			left: -62px;
			width: 44px;
			height: 44px;
			background: #ffebac;
			color: #c69500;
			border-radius: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 4px solid $white;

			&.com {
				background: #c7f8d3;
				color: #00ad0c;
			}
		}

		.work__last_update {
			margin-left: 5px;
			font-size: 14px;
			font-weight: normal;
			font-style: italic;
			color: #767676;
		}
	}

	&__comments {
		margin-top: 20px;
		border-top: 1px solid #e2e5ed;
		padding: 20px 0;
		padding-bottom: 0;
		border-bottom: 1px solid #e2e5ed;
		margin-bottom: 20px;

		.comments {
			&__list {
				margin-bottom: 20px;
			}

			&__item {
				margin-bottom: 20px;
			}

			&__author {
				display: flex;
				align-items: flex-start;
				margin-bottom: 10px;

				.avt {
					width: 35px;
					height: 35px;
					border-radius: 100%;
					overflow: hidden;
					margin-right: 10px;
				}

				.left {
					display: flex;
					align-items: center;
					margin-bottom: 10px;
				}
			}

			&__mess {
				font-weight: normal;
				color: #1c1e21;
				display: inline-block;
				line-height: 16px;
				font-size: 17px;

				.textarea {
					border: none;
					appearance: none;
					width: 100%;
					height: auto;
				}

				p {
					margin-bottom: 5px;
				}
			}

			&__wrapper {
				margin-bottom: 3px;
				width: 100%;
			}

			&__img {
				margin-top: 10px;

				.images_gallerry__item {
					margin-right: 10px;
					margin-bottom: 10px;

					img {
						border-radius: 6px;
						width: 70px;
						height: 70px;
						object-fit: cover;
					}
				}
			}

			&__actions {
				position: relative;
			}

			&__input {
				background: #eff2f5;
				border-radius: 6px;
				height: 46px;
				padding-right: 120px;
				padding-left: 20px;
				font-size: 18px;
			}

			&__action {
				background: none;
				border: none;
				outline: none;
				padding: 5px;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 5px;
				text-align: center;
				width: 40px;

				&.comments__emoji {
					right: 90px;
				}

				&.comments__upload {
					right: 50px;
				}
			}

			&__file {
				width: 200px;
				border-radius: 8px;
				margin-bottom: 5px;
				overflow: hidden;
				position: relative;

				.delete {
					position: absolute;
					top: 5px;
					right: 5px;
					width: 30px;
					height: 30px;
					background: $white;
					border: 1px solid $black;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 100%;
				}

				.icon {
					margin-bottom: 2px;
				}
			}

			&__controls {
				display: flex;
				align-items: center;

				.dot {
					color: #8d949e;
					display: inline-block;
					padding-left: 5px;
					padding-right: 5px;
				}
			}

			&__control {
				display: inline-block;
				color: #385898;
				background: none;
				border: none;
			}

			&__box {
				background-color: #f2f3f5;
				border-radius: 18px;
				padding: 8px 10px;
				color: #1c1e21;
				display: inline-block;
				line-height: 16px;
				white-space: normal;
				word-break: break-word;

				.name {
					font-style: normal;
					font-weight: 600;
					font-size: 16px;
					line-height: 21px;
					color: #385898;
					margin-right: 5px;
				}
			}
		}
	}

	&__avt {
		width: 30px;
		height: 30px;
		border-radius: 100%;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__email {
		.template__list {
			margin-bottom: 20px;

			.nav-link {
				border: 1px solid $main_color;
				color: $main_color;

				&:hover,
				&:active,
				&.active {
					background-color: $main_color;
					color: $white;
				}

				&.SKETCH {
					background: #fff3cd;
					border-color: #fff3cd;
					color: $black;
				}

				&.SKETCH_REVIEW {
					background: #ffebe0;
					border-color: #ffebe0;
					color: $black;
				}

				&.COLOR {
					background: #f4ebff;
					border-color: #f4ebff;
					color: $black;
				}

				&.COLOR_REVIEW {
					background: #ffe5f4;
					border-color: #ffe5f4;
					color: $black;
				}

				&.EXPORT_FILE,
				&.SEND_FILE {
					background: #ffe8e8;
					border-color: #ffe8e8;
					color: $black;
				}

				&.DONE,
				&.PAID {
					background: #ececec;
					border-color: #ececec;
					color: $black;
				}
			}
		}
	}

	&__tabs {
		border-bottom: 2px solid #e2e5ed;
		margin-bottom: 20px;
	}

	&__tab {
		margin-right: 40px;
		color: $main_color;
		text-transform: uppercase;
		font-size: 16px;
		border: none;
		background: none;
		font-weight: 600;
		color: #767676;
		padding: 5px 0;
		margin-bottom: -2px;
		border-bottom: 2px solid #e2e5ed;

		&.active,
		&:hover,
		&:active {
			color: $main_color;
			border-bottom: 2px solid $main_color;
		}
	}

	&__content {
		display: none;

		&.active {
			display: block;
		}
	}

	&__delivery {
		.deli__photos {
			.images_gallerry__item {
				width: 100px;
				height: 100px;
				overflow: hidden;
				border-radius: 8px;
				margin-right: 10px;
				margin-bottom: 10px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
}
