  cursor: pointer;
  user-select: none;
  margin-bottom: 0; }
  .cus-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
  .cus-checkbox .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }
    .cus-checkbox .checkmark:after {
      content: '';
      display: none;
      width: 5px;
      height: 10px;
      border: solid #337fd7;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg); }
  .cus-checkbox input:checked ~ .checkmark:after {
    display: block; }

